@mixin publishers--tablet-and-below {
  @include tablet-and-below {
    flex-wrap: wrap;
    align-items: flex-start;
    overflow-x: hidden;

    .articles {
      @include section-padding;
      width: 100%;
    }

    .publishers__mockups {
      @include section-padding;

      flex-basis: 100%;
      width: 100%;
      min-height: 50vh;
      margin: $lem * 2 0;
    }
  }
}
