@mixin publishers--landscape {
  @include landscape {
    .articles {
      width: $text-block-width;
      margin-right: $lem;
    }

    .publishers__mockups {
      width: calc(100% - #{$text-block-width} - #{$lem});
      max-width: 50vw;
      min-height: $lem * 10;
    }

    .publishers__slider {
      margin-top: $lem * 4;
    }
  }
}
