@mixin publishers {
  @include section-fitscreen;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  .articles {
    @include publishers__articles;
  }

  &__mockups {
    @include publishers__mockups;
  }

  &__slider {
    @include publishers__slider;
  }

  @include publishers--landscape;
  @include publishers--desktop;
  @include publishers--tablet-and-below;
  @include publishers--mobile;
}
