@mixin advertisers--landscape {
  @include landscape {
    .articles {
      width: $text-block-width;
      margin-left: $lem * 3;
      order: 2;
    }

    .advertisers__mockups {
      width: calc(100% - #{$text-block-width} - #{$lem * 3});
      max-width: 55vw;
      min-height: $lem * 20;
      order: 1;
    }

    .advertisers__slider {
      order: 3;
      margin-top: $lem * 4;
    }
  }
}
