@mixin advertisers--tablet-and-below {
  @include tablet-and-below {
    flex-wrap: wrap;
    align-items: flex-start;
    overflow-x: hidden;

    .articles {
      @include section-padding;
    }

    .advertisers__mockups {
      @include section-padding;
      transform: translate($lem, $lem);

      flex-basis: 80%;
      min-height: $lem * 24;
      margin: $lem * 4 $lem * 2 0;
    }
  }
}
